<script>
	export default {
		name: 'portfolio-works',
		data() {
			return {
				portfolio: [
					{
						img: 'img/portfolio-image/screems girls.jpg',
						title: 'Ultra Premium Slider',
						tag: [
							'web',
						],
					},
					{
						img: 'img/portfolio-image/pistol.jpg',
						title: 'Western Poster',
						tag: [
							'print',
						],
					},
					{
						img: 'img/portfolio-image/premiumstickers.jpg',
						title: 'Premium Stickers',
						tag: [
							'audio motion graphics',
							'print',
						],
					},
				],
				selectCategories: [
					'All',
				],
			};
		},
		computed: {
			filterTag: function() {
				const tags = this.selectCategories;

				if (tags.indexOf('All') !== -1) {
					return this.portfolio;
				} else {
					return this.portfolio.filter(function(portfolioUnit) {
						return portfolioUnit.tag.filter(function(portfolioTag) {
							return tags.indexOf(portfolioTag) !== -1
						}).length > 0;
					});
				}
			},
		},

	};
</script>

