<template>
	<div>
		<main-header></main-header>
		<main class="main">
			<div class="container flex">
				<main class="content-portfolio">
					<h3 class="h3 line-bottom">Portfolio</h3>

					<portfolio-nav-block></portfolio-nav-block>

					<block-my-works></block-my-works>
					
				</main> <!-- /main .content -->
			</div> <!-- /container -->
		</main> <!-- /main -->
		<footer-block></footer-block>
	</div>
	
</template>

<script>
    import MainHeader from "../assets/components/the-header/main-header";
    import FooterBlock from "../assets/components/the-footer/footer-block";
    import PortfolioNavBlock from "../assets/components/portfolio/portfolio-nav-block/portfolio-nav-block";
    import BlockMyWorks from "../assets/components/portfolio/portfolio-works/portfolio-works";
    export default {
        name: "Portfolio",
        components: {BlockMyWorks, PortfolioNavBlock, FooterBlock, MainHeader}
    }
</script>

<style lang="scss" scoped>
	.content-portfolio {
		width: 100%;
	}
</style>


